import TaxRange from './taxRanges'
import Country from './country'

function withStarting(countryRange) {
  let prev = null
  return countryRange.map(({ min, max, tax }) => {
    prev = {
      min,
      max,
      tax,
      starting: Math.round(!prev ? 0 : (min - prev.min) * prev.tax + prev.starting)
    }
    return prev
  })
}

function findRange({ income, ranges }) {
  let range
  for (let i = 0; i < ranges.length; i++) {
    const { min, max } = ranges[i]
    if (income >= min && (max && income < max || !max)) {
      range = ranges[i]
      break
    }
  }
  return range
}

function getCountryRange(country) {
  return TaxRange[country]
}

function getTax(income, country) {
  const range = findRange({
    income,
    ranges: withStarting(getCountryRange(Country.SG))
  })
  return (income - range.min) * range.tax + range.starting
}

export { getTax }



