import React, { Component } from 'react'
import './App.css'
import AnualIncome from './AnualIncome'
import { getTax } from './constants/tax'
import Country from './constants/country'
import AfterTax from './AfterTax'

class App extends Component {
  state = {
    income: 100000,
    currency: 'SGD'
  }
  handleIncomeChange = state => {
    this.setState(state)
  }
  render() {
    const { income, currency } = this.state
    const tax = getTax(income, Country[currency])
    return (
      <div className="app">
        <header className="app__header">
          <h1>稅後剩多少</h1>
        </header>

        <section className="app__content">
          <AnualIncome
            income={income}
            currency={currency}
            onChange={this.handleIncomeChange} />
        </section>

        <section className="app__result">
          {
            !!income && <AfterTax income={Number(income)} tax={tax} />
          }
        </section>
      </div>
    )
  }
}

export default App



