import React, { PureComponent } from 'react'
import Paper from 'material-ui/Paper'
import Card, { CardActions, CardContent } from 'material-ui/Card'
import Typography from 'material-ui/Typography'
import { withStyles } from 'material-ui/styles'

const styles = theme => ({
  card: {
    minWidth: 275,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    marginBottom: 16,
    fontSize: 14,
    color: theme.palette.text.secondary,
  },
  pos: {
    marginBottom: 12,
    color: theme.palette.text.secondary,
  },
})

class AfterTax extends PureComponent {
  render () {
    const { income, tax, classes } = this.props
    const percentage = Math.round(tax/income*10000)/100
    return (
      <div className='result'>
        <div className='result__block'>
          <Card className={classes.card}>
            <CardContent>
              <Typography className={classes.title}>Total Tax</Typography>
              <Typography variant="headline" component="h2">{ tax } ( {percentage}% )</Typography>
            </CardContent>
          </Card>
        </div>
        <div className='result__block'>
          <Card className={classes.card}>
            <CardContent>
              <Typography className={classes.title}>After Tax</Typography>
              <Typography variant="headline" component="h2">{ (income-tax).toLocaleString() }</Typography>
            </CardContent>
          </Card>
        </div>
      </div>
    )
  }
}

export default withStyles(styles)(AfterTax)


