import React, { PureComponent } from 'react'
import './App.css'
import TextField from 'material-ui/TextField'
import { MenuItem } from 'material-ui/Menu'
import { withStyles } from 'material-ui/styles'

const styles = theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap'
  },
  incomeTextField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 150
  },
  currencyTextField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 120
  },
  currencyMenu: {
    width: 120
  },
})

const currencies = [
  {
    value: 'USD',
    label: 'USD',
  },
  {
    value: 'NTD',
    label: 'NTD',
  },
  {
    value: 'SGD',
    label: 'SGD',
  },
  {
    value: 'EUR',
    label: 'EUR',
  },
  {
    value: 'JPY',
    label: 'JPY',
  },
]

class AnualIncome extends PureComponent {
  state = {
    currency: ''
  }
  handleChange = name => event => {
    const { onChange } = this.props
    onChange({
      [name]: event.target.value,
    })
  }
  render() {
    let { classes, income, currency } = this.props
    return (
      <div className='anual-income'>
        <div className='form'>
          <div className='form__input'>
            <TextField
              id='income'
              type='number'
              label='Income'
              className={classes.incomeTextField}
              value={income}
              onChange={this.handleChange('income')} />
          </div>

          <div className='form__input'>
            <TextField
              id='select-currency'
              select
              label='Currency'
              className={classes.currencyTextField}
              value={currency}
              onChange={this.handleChange('currency')}
              SelectProps={{
                MenuProps: {
                  className: classes.currencyMenu,
                }
              }}>
              {
                currencies.map(option => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))
              }
            </TextField>
          </div>
        </div>
      </div>
    )
  }
}

export default withStyles(styles)(AnualIncome)


